import React from 'react'
import styles from './HighlightComponent.module.css'
import CustomInput from '../CustomInput/CustomInput'
import UploadDescriptionComponent from '../UploadDescriptionComponent/UploadDescriptionComponent'
import CustomButton from '../CustomButton/CustomButton'

const HighlightComponent = () => {
  return (
    <div className={styles.highlight}>
      <h2 className={styles.highlightTitle}> </h2>
      <div className={styles.content}>
        <h4 className={styles.subTitle}>  </h4>
        <div className={styles.highlightContainer}>
          <div className={styles.imageContainer}>
            <h5 className={styles.h5}></h5>
            <div className={styles.image}></div>
            <CustomButton name=' ' />
          </div>
          <div className={styles.highlightDescription}>
            <div className={styles.highlightBlock}>
              <h5 className={styles.h5}></h5>
              <CustomInput placeholder='  ' type='text' />
            </div>
            <div>
              <h5 className={styles.h5}> </h5>
              <UploadDescriptionComponent placeholder='  ' title='' />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default HighlightComponent
