import React from 'react'
import styles from './TimeTable.module.css'

const days = [
  { id: 1, name: '' },
  { id: 2, name: '' },
  { id: 3, name: '' },
  { id: 4, name: '' },
  { id: 5, name: '' },
  { id: 6, name: '' },
  { id: 7, name: '' },
]

const TimeTable = () => {
  return (
    <div className={styles.timeTableContainer}>
      <h4 className={styles.timeTableTitle}></h4>
      <div className={styles.scheduleContainer}>
        <div className={styles.daysBlock}>
          <div className={styles.titleBlock}>
            <h5 className={styles.daysTitle}> </h5>
            <div className={styles.timeTitleContainer}>
              <h5 className={styles.timeTitle}></h5>
            </div>
          </div>

          {days.map((day) => {
            return (
              <div className={styles.dayContainer} key={day.id}>
                <div className={styles.dayName}>{day.name}</div>
                <div className={styles.timeContainer}>
                  <input className={styles.input} type='text' />
                  <p className={styles.dash}>-</p>
                  <input className={styles.input} type='text' />
                  <input className={styles.checkbox} type='checkbox' />
                  <div className={styles.dayOff}></div>
                </div>
              </div>
            )
          })}
        </div>
        <div className={styles.timeBlock}></div>
      </div>
    </div>
  )
}

export default TimeTable
