import React from 'react'
import styles from './MainPage.module.css'
import main from '../../style/common.module.css'
import { PATH } from '../../navigation/path'
import { useNavigate } from 'react-router-dom'
import SearchFunctionalityComponent from '../../components/SearchFunctionalityComponent/SearchFunctionalityComponent'

const blocks = [
  { id: 1, title: ' ', path: PATH.createObjectCardPage },
  { id: 2, title: ' ', path: PATH.createNewsCardPage },
  { id: 3, title: ' ', path: PATH.createRouteCardPage },
  { id: 4, title: ' ', path: PATH.notificationsPage },
  { id: 5, title: '', path: PATH.usersPage },
  { id: 6, title: '', path: PATH.settingsPage },
]

const MainPage = () => {
  const navigate = useNavigate()

  return (
    <div className={styles.main}>
      <div className={main.container}>
        <h1 className={main.title}></h1>
        <div className={styles.content}>
          <h2 className={styles.subTitle}> </h2>
          <div className={styles.functionalBlocks}>
            {blocks.map((block) => {
              const onActiveFunctionalButton = () => {
                navigate(block.path)
              }

              return (
                <div
                  key={block.id}
                  className={styles.functionalBlock}
                  onClick={onActiveFunctionalButton}
                >
                  <p className={styles.functionalBlockText}>{block.title}</p>
                </div>
              )
            })}
          </div>
          <div className={styles.searchContainer}>
            <h3 className={styles.searchTitle}>
                  , , 
            </h3>
            <SearchFunctionalityComponent />
          </div>
        </div>
      </div>
    </div>
  )
}

export default MainPage
