import React from 'react'
import styles from './ContactsComponent.module.css'
import CustomInput from '../CustomInput/CustomInput'
import CustomDoubleInputComponent from '../CustomDoubleInputComponent/CustomDoubleInputComponent'
import CustomButton from '../CustomButton/CustomButton'
import SubmitButton from '../SubmitButton/SubmitButton'

const ContactsComponent = () => {
  return (
    <div className={styles.contactsContainer}>
      <h4 className={styles.contactsTitle}></h4>
      <div className={styles.contactBlock}>
        <div className={styles.contactText}></div>
        <div className={styles.contactInput}>
          <CustomInput placeholder='  ' type='text' />
        </div>
      </div>
      <div className={styles.contactBlock}>
        <div className={styles.contactText}></div>
        <div className={styles.contactInput}>
          <CustomInput placeholder='  ' type='text' />
        </div>
      </div>
      <div className={styles.contactBlock}>
        <div className={styles.contactText}></div>
        <div className={styles.contactInput}>
          <CustomInput placeholder=' ' type='text' />
        </div>
      </div>
      <CustomDoubleInputComponent
        name='  '
        firstPlaceholder=' '
        secondPlaceholder='  '
        firstSubTitle=''
        secondSubTitle=''
        type='text'
      />
      <div className={styles.createContactBtn}>
        <CustomButton name='' />
      </div>
      <SubmitButton name='' />
    </div>
  )
}

export default ContactsComponent
